.container-fluid, body, html {
    height: 100%;
  }
  body {
    font-family: 'Open Sans', sans-serif;
    font-size: 18px;
    color: #010000;
    font-weight: 400;
    font-style: normal;
  }
  a {
    cursor: pointer;
    text-decoration: none;
    -webkit-transition: .4s ease-in-out;
    -moz-transition: .4s ease-in-out;
    -ms-transition: .4s ease-in-out;
    transition: .4s ease-in-out;
  }
  a:hover {
    color: #010000;
    -webkit-transition: .4s ease-in-out;
    -moz-transition: .4s ease-in-out;
    -ms-transition: .4s ease-in-out;
    transition: .4s ease-in-out;
  }
  .container-fluid {
    line-height: 1.428;
  }
  .red {
    color: #1797ff;
  }
  header {
    position: fixed;
    display: block;
    width: 100%;
    z-index: 100;
    top: 0;
    background: #fff;
    color: #fff;
    height: 75px;
  }
  header .logo {
    color: #1797ff;
    display: block;
    position: absolute;
    visibility: hidden;
    top: 50%;
    left: 15px;
    text-decoration: none;
    -webkit-transform: translateY(-50%);
    -moz-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    transform: translateY(-50%);
  }
  header .logo img, h2 {
    visibility: visible;
    /*Tamaño pequeño*/height: 50px;
  }
  header nav, header nav ul, header nav ul li {
    margin: 0;
    padding: 0;
  }
  header nav {
    position: absolute;
    top: 50%;
    right: 15px;
    -webkit-transform: translateY(-50%);
    -moz-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    transform: translateY(-50%);
  }
  header nav ul li {
    list-style-type: none;
    display: inline-block;
    margin-right: 20px;
  }
  header nav.last {
    margin: 0;
  }
  header nav a {
    font-size: 19px;
    line-height: normal;
    color: #1797ff;
  }
  header nav a:active, header nav a:link {
    text-align: center;
    margin: 200px auto;
    -webkit-animation: pulse 1.5s ease-in-out infinite alternate;
    -moz-animation: pulse 1.5s ease-in-out infinite alternate;
    -ms-animation: pulse 1.5s ease-in-out infinite alternate;
    -o-animation: pulse 1.5s ease-in-out infinite alternate;
    animation: pulse 1.5s ease-in-out infinite alternate;
    -webkit-backface-visibility: hidden;
    -moz-backface-visibility: hidden;
    -ms-backface-visibility: hidden;
    -o-backface-visibility: hidden;
    backface-visibility: hidden;
  }
  
  body {
    margin-top: 90px;
    margin-bottom: 30px;
  }
  body .s-blk-shadow {
    text-shadow: 5px 5px 1px rgba(0, 0, 0, .1);
  }
  body a.p-link:hover {
    background: #e6e6e6 !important;
  }
  body .pin-hide-4 {
    cursor: zoom-in;
    cursor: pointer;
  }
  body .pin-hide-4:before {
    content: '****';
  }
  body .pin-hide-4:hover:before {
    content: '';
  }
  body .pin-hide-4:hover:after {
    content: attr(data-code);
  }
  body .line-sm > input {
    line-height: .5;
  }
  body .led-box {
    height: 30px;
    width: 25%;
    margin: 20px -20px;
  }
  body .led-green {
    margin: 0 auto;
    width: 24px;
    height: 24px;
    background-color: #abff00;
    border-radius: 50%;
    box-shadow: rgba(0, 0, 0, .2) 0 -1px 7px 1px,
      inset #304701 0 -1px 9px,
      #89ff00 0 2px 12px;
  }
  body .led-red {
    margin: 0 auto;
    width: 24px;
    height: 24px;
    background-color: #1797ff;
    border-radius: 50%;
    box-shadow: rgba(0, 0, 0, .2) 0 -1px 7px 1px,
      inset #132444 0 -1px 9px,
      #003cff80 0 2px 12px;
    -webkit-animation: blinkRed .5s infinite;
    -moz-animation: blinkRed .5s infinite;
    -ms-animation: blinkRed .5s infinite;
    -o-animation: blinkRed .5s infinite;
    animation: blinkRed .5s infinite;
  }
  @-webkit-keyframes blinkRed {
    from {
      background-color: #1797ff;
    }
    50% {
      background-color: #1797ff;
      box-shadow: rgba(0, 0, 0, .2) 0 -1px 7px 1px,
        inset #132444 0 -1px 9px,
        #003cff80 0 2px 0;
    }
    to {
      background-color: #1797ff;
    }
  }
  @-moz-keyframes blinkRed {
    from {
      background-color: #1797ff;
    }
    50% {
      background-color: #1797ff;
      box-shadow: rgba(0, 0, 0, .2) 0 -1px 7px 1px,
      inset #132444 0 -1px 9px,
      #003cff80 0 2px 0;
    }
    to {
      background-color: #1797ff;
    }
  }
  @-ms-keyframes blinkRed {
    body from {
      background-color: red;
    }
    body 50% {
      background-color: #1797ff;
      box-shadow: rgba(0, 0, 0, .2) 0 -1px 7px 1px,
      inset #132444 0 -1px 9px,
      #003cff80 0 2px 0;
    }
    body to {
      background-color: #1797ff;
    }
  }
  @-o-keyframes blinkRed {
    from {
      background-color: #1797ff;
    }
    50% {
      background-color: #1797ff;
      box-shadow: rgba(0, 0, 0, .2) 0 -1px 7px 1px,
      inset #132444 0 -1px 9px,
      #3498DB 0 2px 0;
    }
    to {
      background-color: #3498DB  ;
    }
  }
  @keyframes blinkRed {
    from {
      background-color: #3498DB  ;
    }
    50% {
      background-color: #1797ff;
      box-shadow: rgba(0, 0, 0, .2) 0 -1px 7px 1px,
      inset #132444 0 -1px 9px,
      #3498DB   0 2px 0;
    }
    to {
      background-color: #3498DB;
    }
  }
  footer {
    position: fixed;
    right: 0;
    bottom: 0;
    left: 0;
    display: block;
    width: 100%;
    z-index: 100;
    background: #3498DB;
    color: #fff;
    height: 50px;
    text-align: center;
    padding: 15px 10px;
  }
  footer a {
    font-size: 15px;
    line-height: normal;
    color: #fff;
  }
  footer a:hover {
    color: #010000   !important;
  }

/*Servicios*/
.sec-title{
  position:relative;
  padding-bottom:40px;
}

.sec-title .title{
  position:relative;
      color: #1797ff;
  font-size:18px;
  font-weight:700;
  padding-right:50px;
  margin-bottom:15px;
  display:inline-block;
  text-transform:capitalize;
}

.sec-title .title:before{
  position:absolute;
  content:'';
  right:0px;
  bottom:7px;
  width:40px;
  height:1px;
  background-color:#bbbbbb;
}

.sec-title h2{
  position:relative;
  font-size:36px;
  font-weight:700;
  line-height:1.5em;
  display:block;
}

.sec-title.light h2{
  color:#ffffff;
}


.work-section {
    position: relative;
    overflow: hidden;
    padding: 120px 0px 80px;
    background-size: cover;
    background-repeat: no-repeat;
    background-attachment: fixed;
    min-height: 100vh;
}
.work-section .auto-container{
  position:relative;
}

.work-section:before{
  position:absolute;
  content:'';
  left:0px;
  top:0px;
  width:100%;
  height:100%;
  display:block;
}

.work-block{
  position:relative;
  margin-bottom:40px;
}

.work-block .inner-box{
  position:relative;
  text-align:center;
  padding:110px 15px;
  background-color:rgba(37,37,37,0.50);
  border:1px solid rgba(255,255,255,0.70);
}

.work-block .inner-box:hover::before{
  height:2000px;
}

.work-block .inner-box:hover::after{
  height:2000px;
}

.work-block .inner-box .icon-box{
  position:relative;
  color:#000;
  font-size:60px;
  line-height:1em;
  margin-bottom:32px;
}

.work-block .inner-box .step{
  position:relative;
  color:#ffffff;
  font-size:14px;
  font-weight:700;
  padding:6px 20px;
  letter-spacing:1px;
  border-radius:50px;
  margin-bottom:50px;
  display:inline-block;
  text-transform:uppercase;
  border:1px solid rgba(255,255,255,0.70);
}

.work-block .inner-box h2{
  position:relative;
  color:#ffffff;
  font-size:20px;
  font-weight:500;
}

.work-block .inner-box .overlay-box{
    position: absolute;
    left: -1px;
    bottom: -1px;
    right: -1px;
    top: -1px;
    z-index: 2;
    display: block;
    opacity: 0;
    background-color: #1797ff;
    -webkit-transition: all 900ms ease;
    -ms-transition: all 900ms ease;
    -o-transition: all 900ms ease;
    transition: all 900ms ease;
    -webkit-transform: perspective(400px) rotateY(-90deg);
    -moz-transform: perspective(400px) rotateY(-90deg);
    -ms-transform: perspective(400px) rotateY(-90deg);
    -o-transform: perspective(400px) rotateY(-90deg);
    transform: perspective(400px) rotateY(-90deg);
    -webkit-transform-origin: right;
    -moz-transform-origin: right;
    -ms-transform-origin: right;
    -o-transform-origin: right;
    transform-origin: right;
    -ms-transition: all 0.5s;
    -moz-transition: all 0.5s;
    -webkit-transition: all 0.5s;
    -o-transition: all 0.5s;
    transition: all 0.5s;
}

.work-block .inner-box .overlay-box .overlay-inner{
  position: absolute;
    left: 0px;
    top: 0px;
    width: 100%;
    height: 100%;
    display: table;
    text-align: left;
    padding: 10px 0px;
    vertical-align: middle;
}

.work-block .inner-box .overlay-box .overlay-inner .overlay-content{
    position: relative;
    display: table-cell;
    vertical-align: middle;
}

.work-block .inner-box:hover .overlay-box{
  opacity: 1;
    -webkit-transform: perspective(400px) rotateY(0deg);
    -moz-transform: perspective(400px) rotateY(0deg);
    -ms-transform: perspective(400px) rotateY(0deg);
    -o-transform: perspective(400px) rotateY(0deg);
    transform: perspective(400px) rotateY(0deg);
}

.work-block .inner-box .overlay-box .overlay-icon {
    position: relative;
    color: #ffffff;
    font-size: 60px;
    line-height: 1em;
    text-align: center;
    margin-bottom: 35px;
    width: 100%;
}
.work-block .inner-box .overlay-box .overlay-step{
  position:relative;
  color:#ffffff;
  font-size:14px;
  font-weight:700;
  letter-spacing:1px;
  margin-bottom:30px;
  display:inline-block;
  text-transform:uppercase;
  background-color:#252525;
  padding:7px 20px 7px 30px;
  border-radius:0px 50px 50px 0px;
}

.work-block .inner-box .overlay-box h3{
  position:relative;
  color:#ffffff;
  font-size:20px;
  font-weight:500;
  padding-left:28px;
  margin-bottom:12px;
}

.work-block .inner-box .overlay-box h3 a{
  position:relative;
  color:#ffffff;
}

.work-block .inner-box .overlay-box .text{
  position:relative;
  color:#ffffff;
  font-size:15px;
  line-height:1.9em;
  padding:0px 28px;
  margin-bottom:15px;
}

.work-block .inner-box .overlay-box .read-more{
  position:relative;
  color:#ffffff;
  font-weight:700;
  font-size:15px;
  padding-left:28px;
}

.work-block .inner-box .overlay-box .read-more .arrow{
  position:relative;
  top:1px;
}

